import { Splitpanes, Pane } from 'splitpanes';
import { dragscroll } from 'vue-dragscroll';
import EntityBlock from './EntityBlock';
import Arrow from './Arrow';
import Spoiler from './Spoiler';
import {
  FORM_RQUEST_CODE,
  CONTROLLER_CODE,
  ACTION_CODE,
  TASK_CODE,
  MODEL_CODE,
  ENTITY_CODE,
  EXCEPTION_CODE,
  MAIN_COLLECTION_CODE,
  EXAMPLE_COLLECTION_CODE,
  MAIN_BUILDER_CODE,
  EXAMPLE_BUILDER_CODE,
  HELPERS_CODE,
  CONTRACT_CODE,
  INTERFACE_CODE,
} from '../constants/code_examples'
import 'splitpanes/dist/splitpanes.css';
import 'highlight.js/styles/tomorrow-night.css';

export default {
  name: 'Scheme',

  components: {
    Splitpanes,
    Pane,
    EntityBlock,
    Arrow,
    Spoiler,
  },

  directives: {
    dragscroll
  },

  data () {
    return {
      FormRequest: null,
      Controller: null,
      Action: null,
      Task: null,
      Model: null,
      Entity: null,
      Exception: null,
      Helpers: null,
      Collection: null,
      Contract: null,
      Interface: null,

      tabs: {
        FormRequest: 1,
        Controller: 2,
        Action: 3,
        Task: 4,
        Model: 5,
        Entity: 6,
        Exception: 7,
        Helpers: 8,
        Collection: 9,
        Builder: 10,
        Contract: 11,
        Interface: 12,
      },
      current_tab: 1,

      code: {
        FormRequest: FORM_RQUEST_CODE,
        Controller: CONTROLLER_CODE,
        Action: ACTION_CODE,
        Task: TASK_CODE,
        Model: MODEL_CODE,
        Entity: ENTITY_CODE,
        Exception: EXCEPTION_CODE,
        Collection: {
          main: MAIN_COLLECTION_CODE,
          example: EXAMPLE_COLLECTION_CODE,
        },
        Builder: {
          main: MAIN_BUILDER_CODE,
          example: EXAMPLE_BUILDER_CODE,
        },
        Helpers: HELPERS_CODE,
        Contract: CONTRACT_CODE,
        Interface: INTERFACE_CODE,
      },
      codeinit: false,
    }
  },

  computed: {
    computed() {
      return !!this.FormRequest &&
             !!this.Controller &&
             !!this.Action &&
             !!this.Task &&
             !!this.Model &&
             !!this.Entity &&
             !!this.Exception &&
             !!this.Helpers &&
             !!this.Collection
    },
  },

  methods: {
    getCode() {
      this.codeinit = true;
    },
    setTab(tab) {
      this.current_tab = tab;
    },
  },

  mounted() {
    this.getCode()
  }
}
</script>



